header {
    background: var(--azul);
    border-bottom: var(--borda) var(--amarelo);
}

header div.menu {
    height: auto;
    padding: 7.5px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

header nav {
    display: none;
    position: absolute;
    top: 50px;
    right: calc(0px);
}

.menu-toggle div {
    height: 3px;
    width: 20px;
    background: var(--branco);
    margin: 2px 0;
    border-radius: 25px;
}

header .menu.on nav {
    display: block;
    display: flex;
    align-items: center;
    çtransform: translateX(50vw);
    height: 100vh;
    width: 80vw;
    background: var(--azul);
}

header .menu.on nav li {
    padding: 10px 25px;
}