@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@700;800&display=swap);
/*Barlow font*/

* {
    margin: 0;
    padding: 0;
    font-family: "Barlow", 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
    background: #EFEFEF;
    background: var(--branco);
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

:root {
    --azul: #191E74;
    --amarelo: #F9B03D;
    --preto: #222222;
    --branco: #EFEFEF;
    --borda: solid 2.5px;
}

.container-lg {
    width: 92.5%;
    max-width: 1000px;
    height: auto;
    margin: auto;
}

.container-wd {

}

.logo {
    width: 20%;
    min-width: 90px;
    max-width: 170px;
}

.titulo {
    font-weight: 800;
    text-transform: uppercase;
}

.texto-bold {
    font-weight: 700;
}

.texto-branco {
    color: #EFEFEF;
    color: var(--branco);
}

.texto-preto {
    color: #222222;
    color: var(--preto);
}
header {
    background: var(--azul);
    border-bottom: var(--borda) var(--amarelo);
}

header div.menu {
    height: auto;
    padding: 7.5px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

header nav {
    display: none;
    position: absolute;
    top: 50px;
    right: calc(0px);
}

.menu-toggle div {
    height: 3px;
    width: 20px;
    background: var(--branco);
    margin: 2px 0;
    border-radius: 25px;
}

header .menu.on nav {
    display: block;
    display: flex;
    align-items: center;
    çtransform: translateX(50vw);
    height: 100vh;
    width: 80vw;
    background: var(--azul);
}

header .menu.on nav li {
    padding: 10px 25px;
}
