/*Barlow font*/
@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@700;800&display=swap');

* {
    margin: 0;
    padding: 0;
    font-family: "Barlow", 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
    background: var(--branco);
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

:root {
    --azul: #191E74;
    --amarelo: #F9B03D;
    --preto: #222222;
    --branco: #EFEFEF;
    --borda: solid 2.5px;
}

.container-lg {
    width: 92.5%;
    max-width: 1000px;
    height: auto;
    margin: auto;
}

.container-wd {

}

.logo {
    width: 20%;
    min-width: 90px;
    max-width: 170px;
}

.titulo {
    font-weight: 800;
    text-transform: uppercase;
}

.texto-bold {
    font-weight: 700;
}

.texto-branco {
    color: var(--branco);
}

.texto-preto {
    color: var(--preto);
}